@import url('https://fonts.googleapis.com/css2?family=Silkscreen&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.navbar {
  font-family: 'Silkscreen', cursive;
}

#chatName {
  font-family: 'Lobster', cursive;
}

.clearBtn {
  color: #000;
  font-size: 15px;
  background-color: #ffffff;
  border-color: #158fa3;
  margin-right: 10px;
  padding: 4px 8px;
  border-radius: 6px;
}

.clearBtn:hover {
  color: #fff;
  background-color: #2d9fb6;
  border-color: #2f8b8b;
}

.logout_btn {
  color: #000;
  font-size: 15px;
  background-color: #ffe738;
  margin-right: 10px;
  padding: 4px 8px;
  border-radius: 6px;
}

/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset rgb(255, 255, 255);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #0dcaf0;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #14acca;
}

.chat_card {
  width: 60%;
}

.chat_screen {
  height: 450px;
  overflow-Y: scroll;
  overflow-X: hidden;
}

.chat_screen_text h5 {
  font-size: 18px;
}

@media (max-width: 700px) {
  .chat_card {
    width: 90%;
  }

  .chat_screen_text h5 {
    font-size: 15px;
  }

  .clearBtn {
    color: #000;
    font-size: 12px;
    background-color: #ffffff;
    border-color: #158fa3;
    margin-right: 10px;
    padding: 4px 8px;
    border-radius: 6px;
  }

  .logout_btn {
    color: #000;
    font-size: 12px;
    background-color: #ffe738;
    margin-right: 10px;
    padding: 4px 8px;
    border-radius: 6px;
  }
}

@media (max-width: 480px) {
  .chat_card {
    width: 95%;
  }

  .chat_screen {
    height: 300px;
    overflow-Y: scroll;
    overflow-X: hidden;
  }

  .chat_screen_text h5 {
    font-size: 15px;
  }
}